
import {
  reactive,
  toRefs,
  onMounted,
  ref,
  watch,
  computed,
  defineComponent,
} from "vue";
import Modal from "@/components/Modal.vue";
import Timer from "@/components/Timer.vue";
import Menu from "@/components/Menu.vue";
import { useMainStore } from "../store/main";
import { InitData } from "../types/main";
export default defineComponent({
  name: "MineSweeper",
  components: {
    Modal,
    Timer,
    Menu,
  },
  setup() {
    const store = useMainStore();
    const state = reactive(new InitData());

    document.addEventListener("keydown", function ({ key }) {
      console.log(key);
      if (key === "Escape") {
        exitFullScreen();
      }
    });

    watch(
      () => state.hasStart,
      (newV, oldV) => {
        if (newV) {
          //开始游戏
          timerRef.value && timerRef.value.start();
        } else {
          //结束游戏
          timerRef.value && timerRef.value.stop();
        }
        console.log("监听", newV, oldV);
      }
    );

    watch(
      () => state.mode,
      (newV) => {
        if (newV === 1) {
          state.mineCount = 10;
          state.colCount = 8;
          state.rowCount = 8;
        } else if (newV === 2) {
          state.mineCount = 40;
          state.colCount = 16;
          state.rowCount = 16;
        } else if (newV === 3) {
          state.mineCount = 99;
          state.colCount = 30;
          state.rowCount = 16;
        }
        restart();
        store.addAlert({
          type: "secondary",
          content: `难度-${newV == 1 ? "初级" : newV == 2 ? "中级" : "高级"}`,
        });
      }
    );

    const timerRef = ref<InstanceType<typeof Timer>>();

    //获取在坐标面板内部的坐标
    const getPosition = (x: number, y: number) => {
      const aroundPos = [
        [x - 1, y - 1],
        [x - 1, y],
        [x - 1, y + 1],
        [x, y - 1],
        [x, y + 1],
        [x + 1, y - 1],
        [x + 1, y],
        [x + 1, y + 1],
      ];
      return aroundPos.filter((arr) => {
        return (
          arr[0] >= 0 &&
          arr[0] < state.rowCount &&
          arr[1] >= 0 &&
          arr[1] < state.colCount
        );
      });
    };

    //初始化雷区
    const initMine = () => {
      state.mineArr = [[]];
      for (let i = 0; i < state.rowCount; i++) {
        state.mineArr[i] = [];
        for (let j = 0; j < state.colCount; j++) {
          state.mineArr[i][j] = {
            num: 0, //周围雷数
            open: false, //是否已打开
            hasLoop: false, //是否已递归
            flag: false, //是否已标雷
            light: false, //是否高亮
          };
        }
      }

      for (let i = 0; i < state.mineCount; i++) {
        let [row, col] = Random(state.rowCount, state.colCount);
        //如果该位置有雷，则重新获取随机坐标
        while (state.mineArr[row][col].num === 10) {
          [row, col] = Random(state.rowCount, state.colCount);
        }
        state.mineArr[row][col].num = 10;
        //设置雷之后，把周围一圈非雷方块数字+1

        const pos = getPosition(row, col);

        pos.forEach((item) => {
          const [x, y] = item;
          if (state.mineArr[x][y].num !== 10) {
            state.mineArr[x][y].num++;
          }
        });
      }
      console.log(state.mineArr);
      state.flagNum = 0;
    };

    //生成随机整数 [0,max)
    function Random(max_r: number, max_c: number): [x: number, y: number] {
      let x = Math.floor(Math.random() * max_r);
      let y = Math.floor(Math.random() * max_c);
      return [x, y];
    }

    //点击方块
    const open = (row: number, col: number) => {
      if (!state.hasStart) {
        state.hasStart = true;
      }
      //如果点击的方块已插旗，则不触发
      if (state.mineArr[row][col].flag) {
        return false;
      }
      //点击的方块设置为打开
      state.mineArr[row][col].open = true;
      //点击的为雷
      if (state.mineArr[row][col].num === 10) {
        state.failModalVisible = true;
        //暂停计时器
        timerRef.value && timerRef.value.pause();
        return false;
      } else {
        const pos = getPosition(row, col);
        console.log(pos);
        //遍历坐标，如果发现有一个值为10则停止递归
        loop(pos);
      }
      isFinish();
    };

    //判断是否游戏结束  成功or失败
    const isFinish = () => {
      //判断还有几个方块未打开
      let count = 0;
      for (let x = 0; x < state.mineArr.length; x++) {
        for (let y = 0; y < state.mineArr[x].length; y++) {
          if (!state.mineArr[x][y].open) {
            count = count + 1;
          }
          //只要有一个方块open和雷数10同时出现  则失败
          if (state.mineArr[x][y].open && state.mineArr[x][y].num === 10) {
            state.failModalVisible = true;
            //暂停计时器
            timerRef.value && timerRef.value.pause();
            return false;
          }
        }
      }
      //未打开的方块数<=总雷数 则成功
      if (count <= state.mineCount) {
        state.successModalVisible = true;
        //暂停计时器
        timerRef.value && timerRef.value.pause();
        let map: { [keyName: string]: string } = {
          1: "cj",
          2: "zj",
          3: "gj",
        };
        let record: { [keyName: string]: number } = {};
        if (timerRef.value) {
          record[map[state.mode]] = timerRef.value.allSecond();
        }
        store.refreshRecord(record);
        return false;
      }
    };

    //标记旗
    const setFlag = (x: number, y: number) => {
      if (!state.hasStart) {
        state.hasStart = true;
      }
      if (!state.mineArr[x][y].flag) {
        state.mineArr[x][y].flag = true;
        state.flagNum++;
      } else {
        state.mineArr[x][y].flag = false;
        state.flagNum--;
      }
    };

    //使用递归打开方块
    const loop = (pos: Array<Array<number>>) => {
      //周围方块是否存在雷
      const bool = pos.every((item) => {
        const [x, y] = item;
        return state.mineArr[x][y].num !== 10;
      });

      if (bool) {
        pos.forEach((item) => {
          const [x, y] = item;
          if (!state.mineArr[x][y].hasLoop) {
            state.mineArr[x][y].open = true;
            state.mineArr[x][y].hasLoop = true;
            const _pos = getPosition(x, y);
            loop(_pos);
          }
        });
      } else {
        return false;
      }
    };

    //重新开始
    const restart = () => {
      initMine();
      state.hasStart = false;
      store.addAlert({ type: "success", content: "游戏已重新开始" });
    };

    //当鼠标在已打开方块上按下时，周围未打开方块显示效果
    //const showAround = (x, y) => {
    //  const pos = getPosition(x, y);
    //  pos.forEach((item) => {
    //    const [x, y] = item;
    //    if (!state.mineArr[x][y].open) {
    //      state.mineArr[x][y].light = true;
    //    }
    //  });
    //};
    ////当鼠标在已打开方块上按下时，周围未打开方块显示效果
    //const unShowAround = (x, y) => {
    //  const pos = getPosition(x, y);
    //  pos.forEach((item) => {
    //    const [x, y] = item;
    //    if (!state.mineArr[x][y].open) {
    //      state.mineArr[x][y].light = false;
    //    }
    //  });
    //};

    //已标雷的情况下，点击未打开方块时，如果数字等于周围一圈标雷数量，则自动打开周围一圈未标雷的方块
    const openFlag = (_x: number, _y: number) => {
      console.log("点击的坐标", _x, _y);
      const pos = getPosition(_x, _y);
      let flagCount = 0;
      //统计周围一圈标了几个旗
      pos.forEach((item) => {
        const [x, y] = item;
        if (state.mineArr[x][y].flag) {
          flagCount = flagCount + 1;
        }
      });
      //如果点击的数字等于周围旗数
      if (state.mineArr[_x][_y].num === flagCount) {
        //遍历周围一圈，把未插旗的方块打开
        pos.forEach((item) => {
          const [x, y] = item;
          if (!state.mineArr[x][y].flag) {
            state.mineArr[x][y].open = true;
            //打开一个方块就判断一次
            return isFinish();
          }
        });

        //去除已插旗的方块
        pos.forEach((item, index) => {
          if (state.mineArr[item[0]][item[1]].flag) {
            pos.splice(index, 1);
          }
        });
        console.log(pos);
        //递归点击的方块
        loop(pos);
      }

      isFinish();
    };

    //游戏区的动态样式
    const gameBoxStyle = computed(() => {
      let gridStyle = {
        gridTemplateColumns: `repeat(${state.colCount}, ${store.set.size}px)`,
        gridTemplateRows: `repeat(${state.rowCount}, ${store.set.size}px)`,
      };
      let fullScreenStyle = {
        position: "fixed",
        top: 0,
        left: 0,
        width: "100vw",
        height: "100vh",
        paddingTop: "70px",
      };
      if (store.set.fullScreen) {
        return Object.assign({}, gridStyle, fullScreenStyle);
      } else {
        return gridStyle;
      }
    });

    //退出全屏
    const exitFullScreen = () => {
      store.set.fullScreen = false;
    };

    initMine();

    return {
      ...toRefs(state),
      open,
      setFlag,
      restart,
      openFlag,
      timerRef,
      store,
      gameBoxStyle,
      exitFullScreen,
    };
  },
});
