
import { defineComponent } from "vue";
export default defineComponent({
  name: "ModalComp",
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
    },
    content: {
      type: String,
    },
    //第一个按钮的文案
    btn1: {
      type: String,
      default: "确定",
    },
  },
  emits: ["update:visible", "confirm"],
  setup(props, { emit }) {
    //点击关闭
    const close = () => {
      emit("update:visible", false);
    };
    const confirm = () => {
      emit("update:visible", false);
      emit("confirm");
    };
    return {
      close,
      confirm,
    };
  },
});
